import React from 'react';
import './SearchBar.css';

class SearchBar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {  
            input: ""
        }
        this.onType = this.onType.bind(this)
        this.onSearchBarSubmit = this.onSearchBarSubmit.bind(this)
    }

    onSearchBarSubmit(event){
        event.preventDefault();
        this.props.onFormSubmit(this.state.input);
    }

    onType (event) {
        var input = event.target.value;
        this.setState({input})
    }

    render() { 
        return ( 
            <div style={{backgroundColor: '#DAF7A6' }} id="SearchBar" className="search-bar ui segment">
                <h3>SearchBar</h3>
                <form className="ui form" onSubmit={this.onSearchBarSubmit} >
                    <input className="field input-field" type="text" value={this.state.input} id='search' onChange={this.onType} style={{fontFamily:"inherit", fontSize:"15px", backgroundColor:"#FFFCCB", color:"#C70039", width:"35%"}}/>
                </form>
            </div>
         );
    }
}
 
export default SearchBar;