import React from 'react';

class VideoDetail extends React.Component {
    
    render() { 
        if (!this.props.videoItem) {
            return (
                <h1>Search and Click on a video!</h1>
            );
        }
        else{
            const videoSrc = `https://www.youtube.com/embed/${this.props.videoItem.id.videoId}`;
            return ( 
                <div className="ui segment"style={{backgroundColor: '#DAF7A6'}}>
                    <div className="ui embed">
                        <iframe title={this.props.videoItem.snippet.title} src={videoSrc}/>
                    </div>
                    <div className="ui segment" style={{backgroundColor: '#DAF7A6'}}>
                        <h1>{this.props.videoItem.snippet.title}</h1>
                        <p>{this.props.videoItem.snippet.description}</p>
                    </div>
                </div>
            );
        }
    }
}
 
export default VideoDetail;