import React from 'react';
import './VideoItem.css';

class VideoItem extends React.Component {
    constructor(props) {
        super(props);
        this.state = {  }

        this.onVideoSelectedFromItem = this.onVideoSelectedFromItem.bind(this);

    }

    onVideoSelectedFromItem (event) {
        console.log(this.props.video);
        this.props.onVideoSelect(this.props.video);
    }

    render() { 
        return ( 
            <div key={this.props.id} className="video-item item" onClick={this.onVideoSelectedFromItem}>
                <img className="ui image" alt={this.props.desc} src={this.props.thumbnail}/>
                <div className="content">
                    <div className="header">
                        {this.props.title}
                    </div>
                    {/*<div className="description">{this.props.desc}</div>*/}
                </div>
            </div>
         );
    }
}
 
export default VideoItem;